import { Component } from '@angular/core';
import { APIService } from '../../globals/services/API.service';
import { Hunkhtml} from '../../directives/hunkhtml.directive';
import { CkeditorComponent, } from '../../components/ckeditor/ckeditor.component';
import {HtmlcontentsService} from  '../../globals/services/htmlcontents.service';
import { HunkToolbarComponentComponent } from '../../components/hunk-toolbar-component/hunk-toolbar-component.component';

@Component({
  moduleId: module.id,
  selector: 'app-forgot-password',
  templateUrl: 'forgot-password.component.html',
  styleUrls: ['forgot-password.component.css'],
    directives: [Hunkhtml,CkeditorComponent,HunkToolbarComponentComponent],
    providers: [APIService]
})
export class ForgotPasswordComponent  {
    toolbarshow:boolean=false;
    editorshow:boolean=false;
  constructor(private _APIService:APIService,public _HtmlcontentsService:HtmlcontentsService) {}
  sendReminderMail(email){


    this._APIService.getResponseGET('api/user/passwordreminder/'+email,'').subscribe(
                    data=> {
                  alert('Az email ki lett kï¿½ldve!');
                },
                    error=> {
                    console.log(error);
                })
  }
    onToolbarLeave(event){
        this.toolbarshow=false;
    }
    onChangeView(viewMode){
        this.editorshow=(viewMode=='edit')?true:false;
    }
    onValueChanged(content){
        this._HtmlcontentsService.contents.forgotpass.content=content;
    }
    saveContent(){

        this._HtmlcontentsService.setContent( this._HtmlcontentsService.contents.forgotpass.id,this._HtmlcontentsService.contents.forgotpass.content);
        this.editorshow=false;
    }
}
